import React from "react"

const handleColor = (category) => {
    switch(category) {
        case 'desenvolvimento':
          return '#ff0000' 
        case 'arquitetura': 
          return '#6505ad'
        case 'devops':
          return '#298c18'
        default: 
          return '#ff0000'
    }
} 

const Tag = (props) => props.category ? <span style={{
    marginLeft: '2%',
    fontSize: '11px',
    padding: '2px 5px', 
    background: handleColor(props.category), 
    borderRadius: '3px', 
    color: '#FFF'
  }}>{props.category}</span> : ''

  export default Tag